<template>
  <div class="content">
    <van-tabs v-model="active" swipeable sticky color="#1989fa" @change="changeTab">
      <van-tab v-for="(item, index) in tabList" :title="item.tabName" :key="index">
        <vanTable class="itemCell" v-for="(item, index) in list" :key="index" :item="item"/>
      </van-tab>
    </van-tabs>
  </div>
</template>

<script>
import vanTable from "@/components/vanTab";
import { Skeleton } from "vant";

export default {
  name: "homeDetail",
  components: {
    vanTable,
    Skeleton
  },
  data() {
    return {
      active: 0,
      tabList: [
        { tabName: "主项" },
        { tabName: "附项" },
        { tabName: "商品" },
        { tabName: "套餐" }
      ],
      list: [],
      list1: [
        {
          typeSort: "类型排序",
          num: "点数/总占比",
          business: "净营业额/占比",
          item: [
            {
              name: "中医足疗",
              type: "足浴类",
              num: "36",
              numB: 20,
              money: "55555",
              moneyB: 50
            },
            {
              name: "中医足疗",
              type: "足浴类",
              num: "36",
              numB: 20,
              money: "55555",
              moneyB: 70
            }
          ]
        }
      ]
    };
  },
  created() {
    this.list = this.list1;
  },
  methods: {
    changeTab(e) {
      switch (e) {
        case 0:
          this.list = this.list1;
          break;
        case 1:
          this.list = [];
          break;
        case 2:
          this.list = this.list1;
          break;
        case 3:
          this.list = [];
          break;
      }
    }
  }
};
</script>

