<template>
  <div class="vanTab">
    <div class="cellHeader">
      <div class="cell">{{item.typeSort}}</div>
      <div class="cell">{{item.num}}</div>
      <div class="cell">{{item.business}}</div>
    </div>
    <div
      v-for="(itemName, indexs) in item.item"
      :key="indexs"
      :class="['cellContent', indexs % 2 === 0 ? 'borderBottomLine':'']"
    >
      <div class="columnCell">
        <div class="cell progressCell">
          <span v-if="itemName.img">{{indexs + 1}}</span>
          <img v-if="itemName.img" :src="itemName.img" width="20px" height="20px" />
          {{itemName.name}}
        </div>
        <div class="cell" :style="itemName.img ? 'display:none':''">{{itemName.type}}</div>
      </div>
      <div class="columnCell">
        <div class="cell">{{itemName.num}}</div>
        <div class="cell progressCell">
          {{itemName.numB}}%
          <div class="progress">
            <van-progress pivot-text color="#1989fa" :percentage="itemName.numB" />
          </div>
        </div>
      </div>
      <div class="columnCell">
        <div class="cell">{{itemName.money}}</div>
        <div class="cell progressCell">
          {{itemName.moneyB}}%
          <div class="progress">
            <van-progress pivot-text color="#FF9800" :percentage="itemName.moneyB" />
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { Progress } from "vant";
export default {
  name: "vanTable",
  props: ["item"],
  components: {
    "van-progress": Progress
  }
};
</script>

<style lang="scss" scoped>
.cellHeader {
    display: flex;
    flex-direction: row;
    padding: 10px;
    @include sc(0.80rem,$fColor);
    background-color: $sc;
  }

  .cellHeader .cell {
    text-align: left;
    margin-left: 7px;
  }

  .cellContent {
    display: flex;
    flex-direction: row;
    background-color: #ffffff;
    font-size: 14px;
    padding: 5px 10px;
    // border-bottom: 0.02rem solid $sc;
  }

  .columnCell {
    display: flex;
    flex-direction: column;
    padding: 5px 0px;
    @include sc(0.85rem,$fColor);
    flex: 1;
  }

  .columnCell .cell:first-child {
    @include sc(0.85rem,$fColor);
    font-weight: bold;
  }

  .cell {
    flex: 1;
    text-align: left;
    margin: 0px 5px;
  }

  .progressCell {
    display: flex;
    flex-direction: row;
    align-items: center;
  }

  .progress {
    width: 50%;
    margin-left: 15px;
  }
</style>